@import 'variables.scss';
@import url('https://fonts.googleapis.com/css?family=Karla');

/* ---------- Basic Layout Styles ----------- */

html,
body,
#page {
  height: 100%;
  font-family: 'Karla', sans-serif;
}

#page-wrapper {
  min-height: 100%;
  min-width: 960px;
}

#header {
  width: 100;
  div.section {
    padding: 1.1rem;
    margin: 0px 15rem;
    margin-left: auto;
    margin-right: auto;
  }
}

#featured div.section,
#messages div.section,
#main,
#triptych,
#footer-columns,
#footer {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.region-header {
  float: right;
  /* LTR */
  margin: 0 5px 10px;
}

.with-secondary-menu .region-header {
  margin-top: 3em;
}

.without-secondary-menu .region-header {
  margin-top: 15px;
}

#secondary-menu {
  position: absolute;
  right: 0;
  /* LTR */
  top: 0;
  width: 480px;
}

#content,
#sidebar-first,
#sidebar-second,
.region-triptych-first,
.region-triptych-middle,
.region-triptych-last,
.region-footer-firstcolumn,
.region-footer-secondcolumn,
.region-footer-thirdcolumn,
.region-footer-fourthcolumn {
  display: inline;
  float: left;
  /* LTR */
  position: relative;
}

.one-sidebar #content {
  width: 720px;
}

.two-sidebars #content {
  width: 480px;
}

.no-sidebars #content {
  width: 960px;
  float: none;
}

#sidebar-first,
#sidebar-second {
  width: 240px;
}

#main-wrapper {
  min-height: 300px;
}

#content .section,
.sidebar .section {
  padding: 0 15px;
}

#breadcrumb {
  margin: 0 15px;
}

.region-triptych-first,
.region-triptych-middle,
.region-triptych-last {
  margin: 20px 20px 30px;
  width: 280px;
}

#footer-wrapper {
  padding: 35px 5px 30px;
}

.region-footer-firstcolumn,
.region-footer-secondcolumn,
.region-footer-thirdcolumn,
.region-footer-fourthcolumn {
  padding: 0 10px;
  width: 220px;
}

#footer {
  width: 940px;
  min-width: 920px;
}

.full-width-image {
  background: url(http://www.ocarreteiro.com.br/wp-content/uploads/2016/11/sest-senat-curso.jpg);
  width: 100%;
  height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
}

.neutral-background {
  background: $neutral;
}

.events-section {
  height: 4em;
  padding: 2em;
}

.our-actions {
  text-align: center;
  .icon {
    img {
      max-width: 150px;
    }
  }
}

.our-mission {
  text-align: center;
  text-align: -webkit-center;
  margin-bottom: 1em;
  border-bottom: 1px solid $neutral-dark;
  .activity {
    background: #fff;
    border-radius: 5px;
    margin-bottom: 1em;
    padding: 1em;
    .round-icon {
      width: 6em;
      height: 6em;
      border-radius: 100%;
      background: $secondary;
      border: 10px solid #fff;
      position: relative;
      top: -60px;
      .fa {
        line-height: 1.9em;
        color: #fff;
      }
    }
    .title {
      color: $secondary;
      margin-top: -50px;
    }
  }
}
